.container {
  display: grid;
  grid-template-rows: auto;
  grid-template-areas: 
    'header'
    'barkLog'
    'footer';
  font-family: 'arial', sans-serif;
}

header {
  grid-area: header;
  font-size: 4rem;
  display: flex;
  justify-content: center;
}

BarkLog {
  grid-area: barkLog;
}

footer {
  grid-area: footer;
  display: flex;
  justify-content: center;
}